<template>
  <div class="calc-container">
    <div class="calc-header">Kalkulator potrzebnego materiału</div>
    <div class="calc-body">
      <ul>
        <form @submit.prevent="calcKeystone">
          <li>
            <label for="PLE_parameter">Ilość punktów</label>
            <input id="PLE_parameter" v-model="pointAmount">
          </li>
          <li>
            <label for="keystoneActual">Ilość gniazd komputerowych na punkt:</label>
            <select id="keystoneActual" v-model="keystoneRequirementForActualPoint">
              <option>3</option>
              <option>4</option>
            </select>
          </li>
          <li>
            <label for="standardPoint">Mnożnik punktu:</label>
            <input id="standardPoint" v-model="standardPoint">
          </li>
          <input class="button" type="submit" value="Submit">
        </form>
      </ul>
      <div class="data" v-if="mainFrameAmount !== 0" >
        Potrzebny materiał:<br>
        Obudowa (ramka biała): {{ mainFrameAmount }}<br>
        Support (ramka czarna): {{ supportFrameAmount }}<br>
        Szyld podwójny: {{ doubleFaceAmount }}<br>
        <div  v-bind:class="{ isActive: isActive,}">Szyld pojedyńczy: {{ faceAmount }}</div>
        Keystone: {{ keystoneAmount }}<br>
        Siatka (pończocha): {{ supportKeystoneAmount }}<br>
        Trytytka: {{ zipAmount }}<br>
        Gniazdko elektryczne: {{ electricAmount }}<br>
      </div>
      <p><button @click="shareData">Share MDN!</button></p>

      <p class="result"></p>


    </div>

  </div>
</template>

<script>

export default {
  name: 'calcSys',
  data() {
    return {
      mainFrameAmount: 0,
      supportFrameAmount: 0,
      doubleFaceAmount: 0,
      faceAmount: 0,
      keystoneAmount: 0,
      supportKeystoneAmount: 0,
      zipAmount: 0,
      electricAmount: 0,
      pointAmount: 0,
      keystoneRequirementForActualPoint: '3',
      standardPoint: 2,
      isActive: false,
    }
  },
  methods: {
    calcKeystone() {
      let inputArg = parseInt(this.pointAmount, 10)
      let actualPointKeystoneRequirement = parseInt(this.keystoneRequirementForActualPoint, 10)
      this.mainFrameAmount = inputArg * this.standardPoint
      this.supportFrameAmount = inputArg * this.standardPoint
      if(actualPointKeystoneRequirement > 3) {
        this.doubleFaceAmount = inputArg * this.standardPoint
        this.faceAmount = 0
        this.isActive = true
      } else {
        this.doubleFaceAmount = inputArg
        this.faceAmount = inputArg
      }
      this.keystoneAmount = inputArg * actualPointKeystoneRequirement
      this.supportKeystoneAmount = inputArg * actualPointKeystoneRequirement
      this.zipAmount = inputArg * actualPointKeystoneRequirement
      this.electricAmount = inputArg
    },
    shareData() {
      const shareData = {
        title: 'Materiał na gniazdka',
        text: 'Potrzebny materiał:\n' +
            '        Obudowa (ramka biała): ' + this.mainFrameAmount +'\n' +
            '        Support (ramka czarna): ' + this.supportFrameAmount + '\n' +
            '        Szyld podwójny: ' + this.doubleFaceAmount + '\n' +
            '        Szyld pojedyńczy: ' + this.faceAmount + '\n' +
            '        Keystone: ' + this.keystoneAmount + '\n' +
            '        Siatka: ' + this.supportKeystoneAmount + '\n' +
            '        Trytytka: ' + this.zipAmount + '\n' +
            '        Gniazdko elektryczne: ' + this.electricAmount + '\n' +
            'WIADOMOŚĆ WYGENEROWANA AUTOMATYCZNIE',

      }
      const btn = document.querySelector('button');
      const resultPara = document.querySelector('.result');

// Must be triggered some kind of "user activation"
      btn.addEventListener('click', async () => {
        try {
          await navigator.share(shareData)
          resultPara.textContent = 'MDN shared successfully'
        } catch(err) {
          resultPara.textContent = 'Error: ' + err
        }
      });
    }
  },
};

</script>

